import React from "react";
import '../Api.css';
import Footer from "../../components/footer";
import APITopbar from "../components/api-topbar";
import Sidebar from "../components/sidebar";
import "../css/api-sidebar.css";
import Type from "../components/api-type";
import Prop from "../components/api-prop";
import Func from "../components/api-function";
import SidebarInfo from "../sidebar-info";
import ClassMeta from "../components/class-meta";

const Color = () => {
  return (
    <div className="API">
      <title>API | OSGL</title>
      <APITopbar />

      <div className="sidebar-content">
        <Sidebar links={SidebarInfo} />
      </div>

      <div className="api-content">
        <h1 className="api-header">Color</h1>
        <div className="api-header-line"></div>

        <ClassMeta metadata={{
          core: true,
          threadSafe: true,
        }} />

        <p className="api-paragraph">The Color class handles the manipulation of color values.</p>

        <h1 className="api-header">Types</h1>
        <div className="api-header-line"></div>

        <Type
          name="Color"
          type="number"
          code={`local OSGL = require(path.to.osgl)
local color = OSGL.color

-- All other parameters default to 0,
-- except for alpha, which defaults to 255.
local myAwesomeRedColor = color.new(255)`}
        />

        <h1 className="api-header">Properties</h1>
        <div className="api-header-line"></div>

        <Prop name="Red" fullName="Color.RED" type="Color" desc="A red color." />
        <Prop name="Green" fullName="Color.GREEN" type="Color" desc="A green color." />
        <Prop name="Blue" fullName="Color.BLUE" type="Color" desc="A blue color." />
        <Prop name="White" fullName="Color.WHITE" type="Color" desc="A white color." />
        <Prop name="Black" fullName="Color.BLACK" type="Color" desc="A black color." />
        <Prop name="Yellow" fullName="Color.YELLOW" type="Color" desc="A yellow color." />
        <Prop name="Magenta" fullName="Color.MAGENTA" type="Color" desc="A magenta color." />
        <Prop name="Cyan" fullName="Color.CYAN" type="Color" desc="A cyan color." />
        <Prop name="Transparent" fullName="Color.TRANSPARENT" type="Color" desc="A transparent color." />

        <h1 className="api-header">Functions</h1>
        <div className="api-header-line"></div>

        <Func
          name="new"
          fullName="Color.new"
          params={{
            r: "number?",
            g: "number?",
            b: "number?",
            a: { type: "number?", comment: "Alpha transparency." }
          }}
          returnType="Color"
          desc="Creates a new RGBA color value."
          code={`local OSGL = require(path.to.osgl)
local color = OSGL.color

-- All other parameters are defaulted
-- to 0, except for alpha, which is
-- defaulted to 255.
local myAwesomeRedColor = color.new(255)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="newRGB"
          fullName="Color.newRGB"
          params={{
            r: "number?",
            g: "number?",
            b: "number?",
          }}
          returnType="Color"
          desc="Creates a new RGBA color value from RGB."
          code={`local OSGL = require(path.to.osgl)
local color = OSGL.color

-- All other parameters are defaulted
-- to 0, except for alpha, which is
-- defaulted to 255.
local myAwesomeRedColor = color.new(255)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="fromColor3"
          fullName="Color.fromColor3"
          params={{
            color3: "Color3",
          }}
          returnType="Color"
          desc="Creates a new RGBA color value from a Color3."
          code={`local OSGL = require(path.to.osgl)
local color = OSGL.color

local myAwesomeRedColor = color.fromColor3(Color3.new(1, 0, 0))`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="setR"
          fullName="Color.setR"
          params={{
            c: "Color",
            r: "number",
          }}
          returnType="Color"
          desc="Sets the R channel of a Color."
          code={`local almostRed = color.setR(color.BLACK, 100)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="newG"
          fullName="Color.setG"
          params={{
            c: "Color",
            g: "number",
          }}
          returnType="Color"
          desc="Sets the G channel of a Color."
          code={`local almostGreen = color.setG(color.BLACK, 100)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="newB"
          fullName="Color.setB"
          params={{
            c: "Color",
            b: "number",
          }}
          returnType="Color"
          desc="Sets the B channel of a Color."
          code={`local almostBlue = color.setB(color.BLACK, 100)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="newA"
          fullName="Color.setA"
          params={{
            c: "Color",
            a: "number",
          }}
          returnType="Color"
          desc="Sets the A channel of a Color."
          code={`local almostTransparent = color.setB(color.BLACK, 100)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="read"
          fullName="Color.read"
          params={{
            c: "Color",
          }}
          returnType="(number, number, number, number)"
          desc="Reads the channels of a Color."
          code={`local R, G, B, A = color.read(color.RED)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="r"
          fullName="Color.r"
          params={{
            c: "Color",
          }}
          returnType="Color"
          desc="Reads R channel of a Color."
          code={`local R = color.read(color.RED)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="g"
          fullName="Color.g"
          params={{
            c: "Color",
          }}
          returnType="Color"
          desc="Reads G channel of a Color."
          code={`local G = color.read(color.RED)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="b"
          fullName="Color.b"
          params={{
            c: "Color",
          }}
          returnType="Color"
          desc="Reads B channel of a Color."
          code={`local B = color.read(color.RED)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <Func
          name="a"
          fullName="Color.a"
          params={{
            c: "Color",
          }}
          returnType="Color"
          desc="Reads A channel of a Color."
          code={`local A = color.read(color.RED)`}
          metadata={{
            ThreadSafe: true,
          }}
        />

        <div style={{ height: "55px" }} />
      </div>
      <Footer />
      <div className="grid-background-dark"></div>
    </div>
  );
};

export default Color;
